<template>
  <div>
    <div v-if="comment" class="drawer-content-bottom">
      <div class="comment">{{totalCount}} 条评论</div>
      <div class="read">
        <div v-for="(data,id) in comment" :key="id" class="item">
          <div class="imgbox">
            <img :src="data.avatar1x" @click="handleClick(data.creator)" />
          </div>
          <div class="right">
            <p>
              {{data.username}}
              <span>{{data.createTimeStr}}</span>
            </p>
            <p>{{data.content}}</p>
          </div>
        </div>
        <div v-if="auto" @click="trans()">
          <div class="look" @click="handleMore()">查看更多评论</div>
        </div>
        <div v-if="!auto">
          <div class="look" style="border: 0;">没有更多了</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Axios from 'axios'
export default {
  data () {
    return {
      comment: null,
      totalCount: 0,
      current: 1,
      auto: true,
      once: 10
    }
  },
  methods: {
    handleClick (id) {
      localStorage.setItem('creator', id)
      // console.log(this.$router)
      this.$router.push(`/webpage/${id}`)
    },
    handleMore () {
      this.current++
      Axios.get(
        `/api/comment/getDetailCommentsNew?objType=17&objId=${sessionStorage.getItem(
          'olineId'
        )}&lastId=${sessionStorage.getItem('lastOlineId')}&lastRecommend=0`
      ).then(res => {
        // console.log(res.data.data)
        this.comment = [...this.comment, ...res.data.data.data]
        this.current = this.current
      })
    },
    trans () {
      if (this.totalCount <= this.current * this.once || this.totalCount < this.once) {
        this.auto = !this.auto
      }
    }
  },
  mounted () {
    Axios.get(
      `/api/comment/getDetailCommentsNew?objType=17&objId=${sessionStorage.getItem(
        'olineId'
      )}&lastId=0&lastRecommend=0`
    ).then(res => {
      // console.log(res.data)
      this.comment = res.data.data.data
      this.totalCount = res.data.data.totalCount
      sessionStorage.setItem('lastOlineId', res.data.data.data.pop().id)
    })
  }
}
</script>
<style lang="scss" scoped>
.drawer-content-bottom {
  padding-bottom: 0.5rem;
  background: #fff;
  .comment {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.14rem 0.15rem;
    font-size: 18px;
  }
  .read {
    margin: 0 0.2rem;
    .item {
      padding: 0.2rem 0;
      border-top: 0.01rem solid rgb(223, 223, 223);
      display: flex;
      .imgbox {
        float: left;
        display: flex;
        width: 20%;
        img {
          height: 0.4rem;
          width: 0.4rem;
          border-radius: 50%;
        }
      }
      .right {
        margin-left: 0.1rem;
        float: left;
        width: 80%;
        p {
          &:nth-of-type(1) {
            font-size: 14px;
            color: #333;
            span {
              font-size: 12px;
              color: #999;
            }
          }
          &:nth-of-type(2) {
            font-size: 14px;
            color: #666;
            margin-top: 0.1rem;
          }
        }
      }
      &::after {
        content: "";
        display: block;
        height: 0;
        clear: both;
      }
    }
    .look {
      margin-top: 0.2rem;
      color: #666;
      font-size: 14px;
      text-align: center;
    }
  }
}
</style>
