<template>
  <div>
    <input type="text" placeholder="请输入用户名" v-model="username" @input="input" class="user" />
    <input type="password" placeholder="请输入密码" v-model="password" @input="input" />
    <a href="/#/home" class="home">返回首页</a>
    <a href="/#/register" class="register">去注册</a>
    <button :style="{background:buttonColor}" @click="handleClick()">登录</button>
    <p>-- 登录站酷 --</p>
  </div>
</template>

<script>
import Axios from 'axios'
import { MessageBox, Toast } from 'mint-ui'

export default {
  data () {
    return {
      username: '',
      password: '',
      buttonColor: '#ccc'
    }
  },
  methods: {
    input () {
      if (this.username !== '' && this.password !== '') {
        this.buttonColor = '#ffd100'
      } else {
        this.buttonColor = '#ccc'
      }
    },
    handleClick () {
      if (this.username === '' || this.password === '') {
        alert('请输入用户名和密码')
      }
      Axios.post('/login', {
        username: this.username,
        password: this.password
      }).then(res => {
        // console.log(res.data.login)
        // 存储token
        localStorage.setItem('token', res.data.token)
        if (res.data.login === 0) {
          // alert('用户名或密码错误！')
          MessageBox('提示', '用户名或密码错误!')
        } else {
          Toast({
            message: '登录成功',
            position: 'middle',
            duration: 5000
          })
          this.$router.push('/center')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
div {
  font-size: 0.2rem;
  padding: 0.1rem 0.3rem;
  input {
    display: block;
    width: 100%;
    height: 0.5rem;
    line-height: 0.5rem;
    margin-bottom: 0.2rem;
    padding: 0 0.1rem;
    border: 1px solid #ccc;
    border-radius: 0.1rem;
  }
  input.user {
    margin-top: 0.3rem;
  }
  a {
    display: inline-block;
    height: 1rem;
    color: #ffd100;
  }
  a.home {
    float: left;
  }
  a.register {
    width: auto;
    float: right;
  }
  button {
    font-size: 0.25rem;
    display: block;
    width: 100%;
    height: 0.5rem;
    // background: #ffd100;
    border: none;
    border-radius: 0.1rem;
    color: #666;
  }
  p {
    width: 3.15rem;
    text-align: center;
    position: fixed;
    bottom: 0.1rem;
    color: #ffd100;
    font-size: 0.15rem;
  }
}
</style>
