    <div v-if="datainfo" class="headbox">
      <div class="cover">
        <img src="https://static.zcool.cn/zcool/mobile/images/defaultbannar.jpg" />
      </div>
      <div class="sigle">
        <div class="imgBox">
          <img src="https://img.zcool.cn/community/019ab55abb747ea801218207b2dd78.png" />
        </div>
        <p>{{datainfo.username}}</p>
        <div class="new">
          <img src="https://static.zcool.cn/z/images/svg/honor_tuijian_designer.svg" />
        </div>
        <div class="clarify">|</div>
        <ul>
          <li>
            <span>0</span>
            <span>人气</span>
          </li>
          <li>
            <span>0</span>
            <span>创作</span>
          </li>
          <li>
            <span>0</span>
            <span>粉丝</span>
          </li>
          <li>
            <span>0</span>
            <span>关注</span>
          </li>
        </ul>
      </div>
    </div>
    <div class="bodybox">
      <ul>
        <li>创作</li>
      </ul>
      <div class="create-all">
        <div style="margin-top: .4rem;">
          <img
            src="https://static.zcool.cn/zcool/mobile/images/null-2.png"
            style="width:3rem;margin-left: .4rem;"
          />
        </div>
      </div>
    </div>
    <webpage-footer></webpage-footer>
  </div>
</template>
<script>
import webpageFooter from './webpage/webpage-footer'
import Axios from 'axios'
export default {
  data () {
    return {
      datainfo: null
    }
  },
  // 局部拦截
  beforeRouteEnter (to, from, next) {
    // 在渲染该组件的对应路由被confirm前调用
    // 不能获取组件实例'this'
    // 因为当守卫执行前，组件实例还没被创建
    console.log('进入center之前调用')
    if (!localStorage.getItem('token')) {
      console.log('拦截')
      next('/login')
    } else {
      console.log('放行')
      next()
    }
  },
  mounted () {
    // console.log(this.datainfo)
    Axios({
      url: '/tokenval',
      headers: {
        Authorization: localStorage.getItem('token')
      }
    }).then(res => {
      console.log(res.data)
      let { userDetail } = res.data
      this.datainfo = res.data.userDetail
      console.log(this.datainfo)
      this.$store.commit('changeMyDetail', userDetail)
    })
  },
  components: {
    webpageFooter
  }
}
</script>
