<template>
  <div
    style="height: 0.5rem;background: #ffd100;line-height: 0.5rem;padding: 0 0.15rem;box-sizing: border-box;"
  >
    <a href="/home">
      <img src="https://static.zcool.cn/git_z/z/common/images/svg/logo.svg" />
    </a>
    <div class="headlist" @click="handleClick()">
      <svg class="icon" aria-hidden="true" v-if="!isShow">
        <use xlink:href="#icon-category" />
      </svg>
      <svg class="icon" aria-hidden="true" v-if="isShow">
        <use xlink:href="#icon-close" />
      </svg>
    </div>
    <mt-actionsheet :actions="actions" v-model="sheetVisible">
    </mt-actionsheet>
  </div>
</template>
<script>
export default {
  data () {
    return {
      isShow: false,
      actions: [
        {
          name: '搜索',
          method: this.toSearch
        },
        {
          name: '登录/注册',
          method: this.toLogin
        }
      ],
      sheetVisible: false
    }
  },
  methods: {
    handleClick () {
      // this.isShow = !this.isShow;
      this.sheetVisible = true
      if (localStorage.getItem('token')) {
        this.actions[1].name = '我的主页'
        this.actions[1].method = this.toCenter
        if (this.actions.length < 3) {
          this.actions.push({
            name: '退出登录',
            method: this.toOut
          })
        }
      }
    },
    toCenter () {
      this.$router.push('/center')
    },
    toLogin () {
      this.$router.push('/login')
      this.isShow = false
    },
    toSearch () {
      this.$router.push('/search')
      this.isShow = false
    },
    toOut () {
      localStorage.removeItem('token')
      this.$store.commit('clearMyDetail')
      location.href = '/'
    }
  }
}
</script>
<style lang="scss" scoped>
div {
  a {
    display: block;
    font-size: 0;
    float: left;
    img {
      width: 1rem;
      vertical-align: middle;
    }
  }
  .headlist {
    float: right;
    padding: 0;
    position: relative;
    width: 0.25rem;
    height: 0.25rem;
    top: 0.12rem;
    svg {
      width: 0.25rem;
      height: 0.25rem;
      position: absolute;
    }
  }
  ul.mint-popup {
    height: 1.17rem;
    width: 100%;
    list-style: none;
    font-size: 0.2rem;
    position: fixed;
    top: 0.5rem;
    background: #fff;
    z-index: 99;
    transition: height 2s;
    li.popup {
      color: #000;
      border-bottom: 1px solid #ccc;
      font-size: 13px;
      background: #fff;
      height: 0.48rem;
    }
  }
}
</style>
