<template>
  <div>
    <header>
      <h3>{{detailList.title}}</h3>
      <div class="topic">
        <span>#{{detailList.activity_type_label}}</span>
        <div class="views">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-information" />
          </svg>
          <span>{{detailList.view_count}}</span>
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-pic" />
          </svg>
          <span>{{detailList.person_count}}</span>
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-good" />
          </svg>
          <span>{{detailList.recommend_count + detailList.recommend_count_guest}}</span>
        </div>
      </div>
      <div
        class="creator"
        style="marginTop:.2rem;fontSize:0px;height:.4rem"
        v-if="detailList.memberCard"
      >
        <div class="avatarBox" style="float:left">
          <img :src="detailList.memberCard.avatar1x" style="width:.4rem" />
        </div>
        <span
          style="marginLeft:.1rem;color:#333;fontSize:14px;display:inline-block;marginTop:.15rem"
        >{{detailList.memberCard.username}}</span>
      </div>
    </header>
    <div style="margin:0 .15rem;">
      <div style="padding:.1rem;background:#f8fafa;fontSize:0;color:#333">
        <svg
          class="icon"
          aria-hidden="true"
          style="width:.12rem;height:.12rem;marginRight:.05rem;"
        >
          <use xlink:href="#icon-calendar" />
        </svg>
        <span
          style="fontSize:12px"
        >{{detailList.start_time_label + '——' + detailList.end_time_label }}</span>
        <span style="display:inline-block;float:right;fontSize:12px">{{detailList.notice}}</span>
      </div>
      <p
        style="fontSize:16px;color:#666;marginTop:.2rem;lineHeight:.22rem"
      >{{detailList.description}}</p>
      <div style="marginTop:.3rem;position:relative;height:.43rem">
        <span @click="handleRecommend()" class="recommend">{{recommend}}</span>
      </div>
    </div>
    <h2 style="color:#333;fontSize:18px;margin: .4rem .2rem 0">活动作品({{total}})</h2>
    <activitylist :datalist="datalist"></activitylist>
    <footer>
      <div @click="getData()">查看更多</div>
    </footer>
    <div class="number">
      <comment></comment>
    </div>
    <webpage-footer></webpage-footer>
  </div>
</template>
<script>
import activitylist from '@/components/activitylist'
import Axios from 'axios'
import comment from './activitydetails/comment'
import webpageFooter from './webpage/webpage-footer'
export default {
  data () {
    return {
      detailList: [],
      datalist: [],
      recommend: '推荐',
      total: 0,
      getMore: 1
    }
  },
  mounted () {
    console.log(this.$route.params.activityid)
    Axios.post('/activitydetails', {
      detailsId: this.$route.params.activityid
    }).then(res => {
      console.log(res.data)
      this.detailList = res.data.activitydata.data
      // console.log(this.toatl)
    })
    Axios.post('/works', {
      worksId: this.$route.params.activityid
      // getMore:this.getMore
    }).then(res => {
      console.log(res.data)
      this.datalist = res.data.worksdata.data
      this.total = res.data.worksdata.zcoolCommPage.total
      // this.getData(res.data.worksdata.zcoolCommPage.currentPage)
      //  console.log(this.toatl)
      //  console.log(this.datalist)
    })
  },
  methods: {
    handleRecommend () {
      this.recommend = '已推荐'
    },
    getData () {
      this.getMore++
      Axios.post('/works/getMore', {
        worksId: this.$route.params.activityid,
        getMore: this.getMore
      }).then(res => {
        // console.log(res.data)
        // console.log(this.activitylist.list, res.data.newdata.list)
        this.datalist = [...this.datalist, ...res.data.worksdata.data]
      })
    }
  },
  components: {
    activitylist,
    comment,
    webpageFooter
  }
}
</script>
<style lang="scss" scoped>
div {
  header {
    padding: 0.2rem 0;
    margin: 0 0.15rem;
    h3 {
      font-size: 24px;
      color: #333;
    }
    div.topic {
      margin-top: 0.1rem;
      height: 0.15rem;
      & > span {
        font-size: 13px;
        float: left;
      }
      .views {
        float: right;
        height: 0.15rem;
        font-size: 0;
        color: #999;
        span {
          font-size: 12px;
          margin-left: 0.04rem;
          display: inline-block;
        }
        svg {
          margin-left: 0.14rem;
          width: 0.16rem;
          height: 0.13rem;
        }
      }
    }
    .avatarBox {
      width: 0.4rem;
      height: 0.4rem;
      border-radius: 50%;
      overflow: hidden;
    }
  }
  .recommend {
    display: block;
    width: 1.38rem;
    height: 0.38rem;
    border: 1px solid rgb(51, 51, 51);
    border-radius: 0.67rem;
    line-height: 0.38rem;
    text-align: center;
    font-size: 14px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -0.69rem;
    margin-top: -0.19rem;
  }
  footer {
    padding: 0.2rem 0.12rem 0.36rem;
    display: flex;
    align-items: center;
    justify-content: center;
    div {
      border: 1px solid #666666;
      color: #666;
      border-radius: 0.05rem;
      text-align: center;
      font-size: 14px;
      width: 1.75rem;
      height: 0.4rem;
      line-height: 0.4rem;
    }
  }
  .number {
    padding-top: 0.1rem;
    background: #f5f5f9;
  }
}
</style>
