<template>
    <div>
        <div class="hezi">
           <div class="he2">
             <div class="currentCity">当前城市</div>
             <div class="globalCity">{{city}}</div>
            </div>
        <div class="he3">
             <div class="other">其他城市:</div>
            <ul>
            <li v-for="data in citylist" :key="data.name" @click="handleClick(data.id,data.name)">
              {{data.name}}
            </li>
            </ul>
      </div>
    </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      citylist: [
        {
          name: '北京',
          id: -1
        },
        {
          name: '上海',
          id: 66
        },
        {
          name: '杭州',
          id: 438
        },
        {
          name: '广州',
          id: 452
        },
        {
          name: '深圳',
          id: 454
        },
        {
          name: '全国',
          id: -1
        },
        {
          name: '其他城市',
          id: 0
        }
      ],
      city: '全国'
    }
  },

  methods: {
    handleClick (data, name) {
      this.city = name
      axios({
        url: `job/api/hotPosition.do?cityId=${data}&pageNo=1&pageSize=10`
      }).then(res => {
        this.$emit('myevent', res.data.data.data, name)
        console.log(name)
      })
    }
  }
}
</script>
<style lang="scss" scoped>
 .hezi{
    width:100%;
    color:#fff;
    background:rgba(0,0,0,.8);
    // transition:.5s;
    // overflow:hidden;
    // display: flex;
    // flex-direction: column;
      float:left;
    font-size:16px;
    position: absolute;
    z-index:9999;
    height: 4.6rem;
    transition: .5s;
    top:1rem;
    .he2{padding-top:.6rem;
    .currentCity{
      // flex:0 0 1.6rem;
      padding:0 .2rem;
      float:left;}
    .globalCity{
      float:left;
      background:#ffd101;
      border-radius: .34rem;
      height: .34rem;
      line-height: .34rem;
      width:.8rem;
      text-align: center;
    }
    }
    .he3{padding:0,.2rem,.4rem,.4rem;
    .other{
      width:1.5rem;
      padding-left:.2rem;
      padding-top:.6rem;
    }}
    ul{list-style:none;
    // float:left;
    li{
      border-radius: .34rem;
      height: .34rem;
      line-height: .34rem;
      width:.8rem;
      text-align: center;
      border:1px solid #fff;
      float:left;
      margin:.1rem;
    }}
  }
</style>
