import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    pageObj: '',
    pageObjid: '',
    jobId: '',
    my_detail: {
      id: '',
      name: '',
      tel: ''
    }
  },
  mutations: {
    getPageObj (state, data) {
      state.pageObj = data
    },
    getPageObjid (state, data) {
      state.pageObjid = data
    },
    changeMyDetail (state, data) {
      [state.my_detail.id, state.my_detail.name, state.my_detail.tel] = [data.userid, data.username, data.usertel]
    },
    clearMyDetail (state) {
      [state.my_detail.id, state.my_detail.name, state.my_detail.tel] = ['', '', '']
    }
  },
  actions: {
  },
  modules: {
  }
})
