import Vue from 'vue'

Vue.directive('scroll', {
  inserted (el, bind) {
    window.onscroll = function () {
      //  console.log(document.documentElement.scrollTop || this.document.body.scrollTop)
      if (bind.value.$refs.head && bind.value.$refs.photo) {
        var H = bind.value.$refs.head.offsetHeight + bind.value.$refs.photo.offsetHeight
      }
      // console.log(document.documentElement.scrollTop || this.document.body.scrollTop>=bind.value.myswiper.$el.offsetHeight)
      if ((document.documentElement.scrollTop || this.document.body.scrollTop) >= H) {
        // console.log("fixed")
        bind.value.handleScroll(true)
      } else {
        bind.value.handleScroll(false)
      }
    }
  },
  unbind () {
    window.onscroll = null
  }
})
