<template>
    <div id="box" v-html="datastr">
        {{datastr}}
    </div>
</template>

<script>
import Axios from 'axios'
export default {
  data () {
    return {
      datastr: ''
    }
  },
  mounted () {
    // console.log(this.$route.query.id)
    Axios.get(`/getMemberDescription?id=${this.$route.query.id}`).then(res => {
      // console.log(res.data)
      this.datastr = res.data.data.description
    })
    // console.log(this.$route.params.id)
    // Axios.post('/matchDetail',{
    //   id:`${this.$store.state.jobId}`
    // }).then(res => {
    //   this.datastr = res.data.mydata.data.description
    // })
  }

}
</script>

<style lang="scss" scoped>
  #box{
    padding: .2rem .2rem
  }
</style>
