<template>
  <div id="box">
    <ul
      v-if="datalist.length"
      v-infinite-scroll="loadMore"
      infinite-scroll-disabled="loading"
      infinite-scroll-distance="10"
    >
      <li v-for="data in datalist" :key="data.post_id" @click="handleClick(data.post_id)">
        <h4>{{data.post_name}}</h4>
        <span class="salary">{{data.post_salary}}</span>
        <p>
          <span>{{data.city}}</span>|
          <span>{{data.experienceAndDiplomaName}}</span>
        </p>
        <span class="date">{{data.publishTime}}</span>
      </li>
    </ul>
  </div>
</template>

<script>
import Vue from 'vue'
import Axios from 'axios'
import { InfiniteScroll } from 'mint-ui'
Vue.use(InfiniteScroll)
export default {
  data () {
    return {
      page: 1,
      datalist: []
    }
  },
  mounted () {
    // console.log(this.$route.query.id)
    Axios.get(
      `/job/api/corpwap/corpPostlist.do?corp_uid=${this.$route.query.id}&pageNo=1&pageSize=10`
    ).then(res => {
      this.datalist = res.data.data.data
    })
  },
  methods: {
    loadMore () {
      this.loading = true
      this.page++
      var t = setTimeout(() => {
        Axios.get(`/job/api/corpwap/corpPostlist.do?corp_uid=${this.$route.query.id}&pageNo=${this.page}&pageSize=10`).then(res => {
          // console.log(res.data)
          this.datalist = [...this.datalist, ...res.data.data.data]
          // console.log(this.datalist.length, res.data.page.total)
          if (this.datalist.length === res.data.page.total) {
            clearTimeout(t)
          }
        })
        this.loading = false
      }, 500)
    },
    handleClick (postId) {
      this.$router.push(`/JobDetail/${postId}`)
    }
  }
}
</script>

<style lang="scss" scoped>
#box {
  background: #eee;
  padding: 0.1rem 0;
  ul {
    li {
      background: white;
      margin-bottom: 0.1rem;
      padding: 0.2rem 0.2rem;
      p {
        display: inline-block;
        color: #ccc;
      }
      span.salary {
        display: block;
        height: 0.5rem;
        line-height: 0.5rem;
        float: right;
        color: #f60;
      }
      span.date {
        float: right;
        color: #ccc;
      }
      h4 {
        display: inline-block;
        width: 60%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        height: 0.5rem;
        line-height: 0.5rem;
      }
    }
  }
}
</style>
