<template>
  <div class="drawer-content">
    <div class="drawer-content-middle">
      <div class="drawer-content-top" style="bacground:#fff">
        <nav-detail :pageUrl="pageUrl"></nav-detail>
      </div>
      <div class="slider">
        <div class="drawer-content-center" v-if="dataInfo">
          <p class="p1">{{dataInfo.product.creatorObj.username}}的其他创作</p>
          <swiper :options="productoption" class="productwiper productswiper">
            <div class="swiper-slide" v-for="(data,id) in artical" :key="id">
              <img :src="data.cover1x" alt />
              <p class="text">{{data.title}}</p>
            </div>
          </swiper>
        </div>
      </div>
      <div class="number">
        <comment-detail></comment-detail>
      </div>
    </div>
    <webpage-footer v-if="dataInfo"></webpage-footer>
  </div>
</template>
<script>
import Axios from 'axios'
import swiper from '@/components/Swiper'
import webpageFooter from './webpage/webpage-footer'
import navDetail from './homedetail/nav-detail'
import commentDetail from './homedetail/comment-detail'
export default {
  data () {
    return {
      dataInfo: null,
      artical: null,
      productoption: {
        slidesPerView: 3,
        spaceBetween: 5,
        freeMode: true
      },
      pageUrl: '',
      swiperId: ''
    }
  },
  components: {
    swiper,
    webpageFooter,
    navDetail,
    commentDetail
  },

  methods: {
    handleClick (id) {
      this.$router.push(`/webpage/${id}`)
    }
  },
  mounted () {
    Axios.get(`/api/${sessionStorage.getItem('pageObj')}.html`).then(res => {
      this.dataInfo = res.data.data
    })
    this.swiperId = sessionStorage.getItem('pageId')
    Axios.get(
      `/api/content/other?pageSize=6&objectType=3&objectId=${this.swiperId}`
    ).then(res => {
      this.artical = res.data.data
    })
  }
}
</script>
<style lang="scss" scoped>
.drawer-content {
  position: relative;
  overflow: auto;
  .drawer-content-middle {
    .slider {
      background: #f5f5f9;
      padding-top: 0.1rem;
      .drawer-content-center {
        padding-top: 0.2rem;
        background: #fff;
        .p1 {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 0 0.15rem;
          font-size: 18px;
          color: #333;
        }
        .swiper-wrapper {
          .swiper-slide {
            //   background: yellow;
            img {
              max-width: 100%;
            }
            .text {
              padding-top: 0.1rem;
              color: #000;
              font-size: 16px;
              white-space: nowrap;
              text-overflow: ellipsis;
              overflow: hidden;
            }
          }
        }
      }
    }
    .number {
      padding-top: 0.1rem;
      background: #f5f5f9;
    }
  }
}
</style>
