<template>
  <div>
    <div class="title" v-if="dataInfo">
      <h2>{{dataInfo.product.title}}</h2>
      <div class="time">
        <span class="gray">{{dataInfo.product.publishTimeDiffStr}}</span>
        <div class="detail">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-map" />
          </svg>
          <span>{{dataInfo.product.viewCount}}</span>
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-good" />
          </svg>
          <span>{{dataInfo.product.recommendCount+dataInfo.product.recommendCountGuess}}</span>
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-comments" />
          </svg>
          <span>{{dataInfo.product.commentCount}}</span>
        </div>
      </div>
      <div class="usermes">
        <div class="imgbox">
          <img
            :src="dataInfo.product.creatorObj.avatar1x"
            @click="handleClick(dataInfo.product.creator)"
          />
        </div>
        <div class="right">
          <p>{{dataInfo.product.creatorObj.username}}</p>
          <p>{{dataInfo.product.creatorObj.cityName}} | {{dataInfo.product.creatorObj.professionName}}</p>
        </div>
      </div>
    </div>
    <div class="description" v-if="dataInfo">
      <p v-html="dataInfo.sharewords"></p>
      <div v-for="(data,id) in dataInfo.product.productImages" :key="id" class="imgs">
        <img :src="data.url" alt />
      </div>
      <div class="recommend">
        <i></i><span @click="handleRecommend()">{{recommend}}</span>
      </div>
    </div>
    <div class="create" v-if="dataInfo">
      {{dataInfo.product.typeStr}}-{{dataInfo.product.fieldCateObj.name}}-{{dataInfo.product.subCateObj.name}}
      <p>{{dataInfo.product.copyrightStr}}</p>
    </div>
  </div>
</template>
<script>
import Axios from 'axios'
export default {
  data () {
    return {
      dataInfo: null,
      objectId: '',
      recommend: '推荐'
    }
  },
  methods: {
    handleClick (id) {
      localStorage.setItem('creator', id)
      this.$router.push(`/webpage/${id}`)
    },
    handleRecommend () {
      this.recommend = '已推荐'
    }
  },
  mounted () {
    this.objectId = sessionStorage.getItem('pageObj')
    Axios.get(`/api/${this.objectId}.html`).then(res => {
      this.dataInfo = res.data.data
      this.id = res.data.data.id
    })
  },
  props: ['pageUrl']
}
</script>
<style lang="scss" scoped>
.title {
  padding: 0.26rem 0;
  margin: 0 0.15rem;
  border-bottom: 0.01rem solid #dfdfdf;
  h2 {
    line-height: 0.43rem;
    font-size: 24px;
  }

  .time {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 0.1rem;
    .gray {
      color: #999999;
      font-size: 12px;
    }
    .detail {
      float: right;
      font-size: 12px;
      color: #999;
      .icon {
        margin-left: 0.14rem;
      }
      span {
        margin-left: 0.04rem;
        color: #999;
      }
      &::after {
        content: "";
        display: block;
        height: 0;
        clear: both;
      }
    }
  }
  .usermes {
    margin-top: 0.16rem;
    .imgbox {
      float: left;
      display: flex;
      img {
        height: 0.4rem;
        width: 0.4rem;
        border-radius: 50%;
      }
    }
    .right {
      margin-left: 0.1rem;
      float: left;
      p {
        &:nth-of-type(1) {
          font-size: 14px;
          color: #333;
        }
        &:nth-of-type(2) {
          font-size: 12px;
          color: #999;
        }
      }
    }
    &::after {
      content: "";
      display: block;
      height: 0;
      clear: both;
    }
  }
}
.description {
  font-size: 16px;
  border-bottom: 0.01rem solid #dfdfdf;
  padding: 0.2rem 0;
  margin: 0 0.15rem;
  p {
    color: #666;
    line-height: 0.25rem;
    margin-bottom: 0.2rem;
  }
  .imgs {
    margin: 0.1rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      display: block;
      max-width: 100%;
    }
  }
  .recommend {
    width: 1.35rem;
    height: 0.5rem;
    font-size: 16px;
    background: #ffd100;
    border-radius: 0.35rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0.3rem 1.05rem 0.1rem;
    i {
      display: block;
      width: 0.28rem;
      height: 0.28rem;
      background-image: url(https://static.zcool.cn/zcool/mobile/images/svg/zan.svg);
      background-repeat: no-repeat;
      background-size: 0.28rem 0.28rem;
    }
  }
}
.create {
  margin: 0 0.15rem;
  padding: 0.2rem 0;
  font-size: 14px;
  color: #999;
  p {
    margin-top: 0.1rem;
  }
}
</style>
