<template>
  <div>
    <ul>
      <li>创作</li>
    </ul>
    <div class="create-all" v-if="datalist">
      <div class="create-single" v-for="(data,index) in datalist" :key="index" @click="handleClick(index,data.id)">
        <div class="imgbox">
          <img :src="data.cover1x"  />
        </div>
        <div class="info">
          <h2>{{data.title.replace('&quot;','"')}}</h2>
          <p>{{data.creatorObj.username}}</p>
          <div class="detail">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-map" />
            </svg>
            <span>{{data.viewCount}}</span>
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-good" />
            </svg>
            <span>{{data.recommendCount}}</span>
          </div>
        </div>
      </div>
      <div class="many" v-if="auto" @click="trans()">
        <div class="more" @click="handleMore()">查看更多</div>
      </div>
      <div class="many" v-if="!auto">
        <div class="more" style="border: 0;">没有更多了</div>
      </div>
    </div>
  </div>
</template>
<script>
import Axios from 'axios'
export default {
  data () {
    return {
      datalist: null,
      current: 1,
      objId: [],
      total: 0,
      once: 10,
      auto: true
    }
  },
  mounted () {
    Axios.get(
      `/api//u/${localStorage.getItem('creator')}?sort=8&p=1&ps=10`
    ).then(res => {
      // console.log(res.data.data)
      this.datalist = res.data.data.content
      this.objId = this.datalist.map(items => items.pageUrl.slice(25, -5))
      this.total = res.data.data.total
      // console.log(this.total)
    })
  },
  methods: {
    handleClick (index, id) {
      sessionStorage.setItem('pageObj', this.objId[index])
      sessionStorage.setItem('pageId', id)
      this.$router.push({ name: 'homedetail', params: { objid: id, url: this.objId[index] } })
    },
    handleMore () {
      this.current++
      Axios.get(
        `/api//u/${localStorage.getItem('creator')}?sort=8&p=${
          this.current
        }&ps=10`
      ).then(res => {
        this.datalist = [...this.datalist, ...res.data.data.content]
        this.objId = [...this.objId, ...(res.data.data.content.map(items => items.pageUrl.slice(25, -5)))]
        this.current = this.current
      })
    },
    trans () {
      if (this.total <= this.current * this.once || this.total < this.once) {
        this.auto = !this.auto
      }
    }
  }
}
</script>

<style lang="scss" scoped>
* {
  margin: 0;
  padding: 0;
  font-size: 12px;
}
ul {
  display: flex;
  list-style: none;
  li {
    flex: 1;
    height: 0.2rem;
    line-height: 0.2rem;
    text-align: center;
    font-size: 14px;
    color: #333;
    border-bottom: 2px solid orange;
  }
}
.create-all {
  margin-top: 0.18rem;
  .create-single {
    padding: 0.1rem;
    display: flex;
    .imgbox {
      float: left;
      display: flex;
      img {
        width: 1.6rem;
        height: 100%;
        border-radius: 5%;
      }
    }
    .info {
      margin-left: 0.1rem;
      padding: 0.1rem 0;
      h2 {
        margin-bottom: 0.15rem;
        font-size: 16px;
        color: #333;
        line-height: 0.2rem;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        display: -webkit-box;
      }
      p {
        font-size: 12px;
        color: #333;
      }
      .detail {
        margin-top: 0.05rem;
        font-size: 12px;
        color: #999;
        .icon {
          margin-right: 0.04rem;
        }
        span {
          margin-right: 0.15rem;
          color: #999;
        }
      }
    }
    &::after {
      content: "";
      display: block;
      height: 0;
      clear: both;
    }
  }
  .many {
    padding: 0.2rem 0.1rem 0.36rem;
    .more {
      height: 0.4rem;
      line-height: 0.4rem;
      border: 0.015rem solid #666;
      border-radius: 0.11rem;
      text-align: center;
      margin: 0 0.89rem;
      color: #666;
      font-size: 14px;
    }
  }
}
</style>
