import Vue from 'vue'
import VueRouter from 'vue-router'
import activies from '../views/activies.vue'
import center from '../views/center'
import job from '../views/job.vue'
import match from '../views/match.vue'
import login from '../views/login'
import home from '../views/home'
import JobDetail from '../views/JobDetail'
import search from '../views/search'
import post from '../views/post'
import matchDetail from '../views/matchDetail'
import register from '../views/register'
import company from '../views/company'
import introduce from '../views/company/introduce'
import nowjob from '../views/company/nowjob'
import activitydetails from '../views/activitydetails'
import homedetail from '@/views/homedetail'
import webpage from '@/views/webpage'

Vue.use(VueRouter)

const routes = [
  {
    path: '/center',
    component: center
  },
  {
    path: '/activies',
    component: activies
  },
  {
    path: '/job/',
    component: job
  },
  {
    path: '/JobDetail/:myid',
    component: JobDetail
  },
  {
    path: '/activitydetails/:activityid',
    component: activitydetails
  },
  {
    path: '/job',
    component: job
  },
  {
    path: '/match',
    component: match
  },
  {
    path: '/login',
    component: login
  },
  {
    path: '/home',
    component: home
  },
  {
    path: '/search',
    component: search
  },
  {
    path: '/post',
    name: 'Post',
    component: post
  },
  {
    path: '/matchDetail',
    component: matchDetail
  },
  {
    path: '/register',
    component: register
  },
  {
    path: '/company',
    name: 'Company',
    component: company,
    children: [
      {
        path: '/company/introduce',
        name: 'Introduce',
        component: introduce
      },
      {
        path: '/company/nowjob',
        name: 'Nowjob',
        component: nowjob
      },
      {
        path: '/company',
        redirect: '/company/introduce'
      }
    ]
  },
  {
    // path: '/homedetail/', //  动态路由
    path: '/homedetail/:objid', //  动态路由
    name: 'homedetail',
    component: homedetail
  },
  {
    path: '/webpage/:creatorid', //  动态路由
    component: webpage
  },
  // {
  //   path: '/zcpage/:creatorid', //  动态路由
  //   component: webpage,
  //   children: [
  //     {
  //       path: '/zcpage/create',
  //       component: create
  //     },
  //     {
  //       path: '/zcpage/message',
  //       component: message
  //     }
  //     // {
  //     //   path: '/webpage/:creatorid',
  //     //   redirect: '/webpage/create'
  //     // }
  //   ]
  // },
  {
    path: '*',
    redirect: '/home'
  }

]

const router = new VueRouter({
  // mode: 'history',
  // base: process.env.BASE_URL,
  routes
})

export default router
