<template>
  <div id="box" v-scroll="{
    $refs:$refs,
    handleScroll:handleScroll
    }">
    <header ref="head">公司</header>
    <div class="photo" v-if="dataobj.company" ref="photo">
      <span class="companyHome" @click="toCompanyHome()">站酷主页</span>
      <img :src="dataobj.logo" />
      <p>{{dataobj.company.companyltd}}</p>
      <p class="words">
        <span>{{dataobj.industryAndStageName}}</span>/
        <span>{{dataobj.size_name}}</span>
      </p>
      <button>关注</button>
    </div>
    <companyheader :class="isfixed? 'fixed': ''" :companyId="id" v-if="id"></companyheader>
    <router-view />
  </div>
</template>

<script>
import companyheader from './company/companyheader'
import Axios from 'axios'
import '@/directive/scroll'
export default {
  data () {
    return {
      dataobj: {},
      isfixed: false,
      id: ''
    }
  },
  mounted () {
    // console.log(this.$route.query.id)
    this.id = this.$route.query.id
    // console.log(this.id)
    Axios.get(`/job/api/corpwap/corpDetailData.do?corp_uid=${this.$route.query.id}`).then(
      res => {
        // console.log(res.data.data)
        this.dataobj = res.data.data
        // console.log(this.dataobj.company.companyltd)
      }
    )
  },
  components: {
    companyheader
  },
  methods: {
    handleScroll (isfixed) {
      if (isfixed) {
        this.isfixed = true
      } else {
        this.isfixed = false
      }
    },
    toCompanyHome () {
      this.$router.push('/zcpage')
    }
  }
}
</script>

<style lang="scss" scoped>
* {
  font-size: 0.18rem;
}
div#box {
  header {
    width: 100%;
    height: 0.5rem;
    line-height: 0.5rem;
    background: #ffd100;
    text-align: center;
    font-size: 0.25rem;
  }
  .photo {
    width: 100%;
    height: 2.5rem;
    background: #cccccc;
    text-align: center;
    vertical-align: middle;
    padding-top: 0.35rem;
    background: url("https://static.zcool.cn/job-mobile-web/images/975313cef01cb2834808bcb2d3a88795.jpg");
    position: relative;
    .companyHome{
      position: absolute;
      display: inline-block;
      width: .6rem;
      height: .2rem;
      line-height: .2rem;
      text-align: center;
      border-radius: .05rem;
      background: black;
      color: white;
      font-size: .12rem;
      right: .2rem;
      top: .2rem;
    }
    img {
      width: 1rem;
      height: 1rem;
      border-radius: 50%;
    }
    p {
      color: white;
    }
    .words {
      height: 0.5rem;
      line-height: 0.5rem;
      font-size: 0.1rem;
    }
    button {
      width: 1rem;
      height: 0.35rem;
      background: #ffd100;
      border: none;
      border-radius: 5px;
    }
  }
  .fixed {
    position: fixed;
    top: 0;
    left: 0;
    flex: 1;
    width: 100%;
    height: .5rem;
    line-height: .5rem;
    text-align: center;
    background: white;
  }
}
</style>
