<template>
  <div id="box">
    <ul
      v-infinite-scroll="loadMore"
      infinite-scroll-disabled="loading"
      infinite-scroll-distance="10"
    >
      <li v-for="data in datalist" :key="data.title" @click="handleClick(data.linkAddress)">
        <div class="pic">
          <img :src="data.fullpath_m_cover" />
          <span class="notice">{{data.notice}}</span>
        </div>
        <b>{{data.title}}</b>
        <span class="date">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-calendar" />
          </svg>
          {{data.start_time_label}} - {{data.end_time_label}}
        </span>

        <span class="count">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-account" />
          </svg>
          {{data.product_count}}
        </span>
        <span class="count">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-pic" />
          </svg>
          {{data.view_count}}
        </span>
      </li>
    </ul>
  </div>
</template>
<script>
import Vue from 'vue'
import Axios from 'axios'
import { InfiniteScroll } from 'mint-ui'
Vue.use(InfiniteScroll)
export default {
  data () {
    return {
      page: 1,
      datalist: [],
      loading: false
    }
  },
  methods: {
    loadMore () {
      this.loading = true
      this.page++
      setTimeout(() => {
        Axios.post('/users', {
          page: this.page
        }).then(res => {
          // console.log(res.data)
          // this.datalist = res.data.mydata.result.records;
          this.datalist = [...this.datalist, ...res.data.mydata.result.records]
        })

        this.loading = false
      }, 500)
    },
    handleClick (url) {
      console.log(url)
      location.href = url
    }
  },
  mounted () {
    Axios.post('/users', {
      page: this.page
    }).then(res => {
      // console.log(res.data);
      this.datalist = [...this.datalist, ...res.data.mydata.result.records]
    })
    //   Axios({
    //     url:"/users",
    //     headers:{
    //       page:1
    //     }
    //   }).then(res => {
    //     console.log(res.data);
    //     this.datalist = res.data.mydata.result.records;
    //   })
    // }
  }
}
</script>

<style scoped>
div#box {
  font-size: 0.16rem;
}
li {
  padding: 0.15rem;
}
div.pic {
  position: relative;
}
.pic > span {
  position: absolute;
  bottom: 0.12rem;
  right: 0.1rem;
  display: inline-block;
  padding: 0.03rem;
  background: #ffd100;
  border-radius: 0.03rem;
  color: #555;
}
img {
  width: 100%;
  height: auto;
}
b {
  display: block;
}
span.date {
  font-size: 0.12rem;
  margin-right: 0.6rem;
  color: #666;
}
span.count {
  font-size: 0.12rem;
  float: right;
  padding: 0.05rem 0.15rem;
  color: #999;
}
</style>
