<template>
  <div class="indexbox">
    <h2>首页推荐</h2>
    <ul>
      <li v-for="(data,index) in datalist" :key="data.id" @click="handleClick(index,data.id)">
        <div class="imgbox">
          <img :src="data.cover1x" />
        </div>
        <div class="titlebox">
          <h3>{{data.title}}</h3>
          <span class="username">{{data.creatorObj.username}}</span>
          <!-- <span class="username" v-else>{{data.username}}</span> -->
          <span>
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-information" />
            </svg>
            {{data.viewCount}}
          </span>
          <span>
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-good" />
            </svg>
            {{data.recommendCount}}
          </span>
        </div>
      </li>
    </ul>
    <footer v-if="datalist.length">
      <div @click="getData()">查看更多</div>
    </footer>
  </div>
</template>
<script>
import Axios from 'axios'
export default {
  data () {
    return {
      datalist: [],
      currentP: 1,
      objId: []
    }
  },
  methods: {
    getData () {
      this.currentP++
      Axios.get(
        `/api//discoverListNew?contentType=0&p=${this.currentP}&ps=10&recommendLevel=3&activity=0`
      ).then(res => {
        this.datalist = [...this.datalist, ...res.data.data.content]
        this.objId = [...this.objId, ...(res.data.data.content.map(items => items.pageUrl.slice(25, -5)))]
      })
    },
    handleClick (index, id) {
      // console.log(this.objId[index])
      // console.log(id)
      sessionStorage.setItem('pageObj', this.objId[index])
      sessionStorage.setItem('pageId', id)
      this.$router.push({ name: 'homedetail', params: { objid: id, url: this.objId[index] } })
    }
  },
  mounted () {
    Axios.get(
      '/api//discoverListNew?contentType=0&p=1&ps=10&recommendLevel=3&activity=0'
    ).then(res => {
      this.datalist = res.data.data.content
      this.objId = this.datalist.map(items => items.pageUrl.slice(25, -5))
      // console.log(this.objId)
    })
  }
}
</script>
<style lang="scss" scoped>
div.indexbox{
  margin-top: 0.3rem;
  h2 {
    font-size: 24px;
    padding: 0 0.15rem;
    font-weight: 500;
    height: 0.28rem;
    line-height: 0.28rem;
    background: #fff;
  }
  ul {
    margin-top: 0.1rem;
    li {
      padding: 0.1rem 0.15rem;
      list-style: none;
      // display: flex;
      .imgbox {
        float: left;
        border-radius: 0.05rem;
        margin-top: 0;
        overflow: hidden;
        height: 1.2rem;
        width: 1.59rem;
        img {
          width: 1.59rem;
        }
      }
      .titlebox {
        position: relative;
        margin-left: 0.1rem;
        margin-top: 0;
        padding: 0.1rem 0;
        float: left;
        width: 1.59rem;
        height: 1.2rem;
        h3 {
          font-size: 16px;
          font-weight: 600;
          color: #333;
          width: 100%;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2; // 多行文本溢出隐藏
        }
        span {
          font-size: 12px;
          display: inline-block;
          position: absolute;
          left: 0;
          color: #999;
          &.username {
            bottom: 0.5rem;
            color: #333;
          }
          &:nth-of-type(2){
            bottom: 0.3rem;
          }
          &:nth-of-type(3) {
            bottom: 0.3rem;
            left: 0.65rem;
          }
          svg {
            margin-right: 0.04rem;
          }
        }
      }
      &::after {
        content: "";
        display: block;
        height: 0;
        clear: both;
      }
    }
  }
  footer{
    padding: .2rem .12rem .36rem;
    display: flex;
    align-items: center;
    justify-content: center;
    div{
      border: 1px solid #666666;
      color:#666;
      border-radius: .05rem;
      text-align: center;
      font-size: 14px;
      width: 1.75rem;
      height: .4rem;
      line-height: .4rem;
    }
  }
}
</style>
