<template>
  <div>
    <h2>线上活动</h2>
    <ul>
      <li v-for="data in activitylist" :key="data.id" @click="handleClick(data.id)">
        <section>
        <div class="imgbox">
          <img :src="data.fullpath_m_cover" />
        </div>
        <div class="titlebox">
          <h3>{{data.title}}</h3>
          <span class="username">{{data.memberCard.username}}</span>
          <span>
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-information" />
            </svg>
            {{data.view_count}}
          </span>
          <span>
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-pic" />
            </svg>
            {{data.person_count}}
          </span>
        </div>
        </section>
        <div class="label">
          <span>#{{data.activity_type_label}}</span>
          <span>{{data.notice}}</span>
        </div>
        <div style="height:.01rem;background:#dfdfdf;margin:.2rem .15rem .1rem"></div>
      </li>
    </ul>
    <footer v-if="auto" @click="trans()">
      <div @click="getData()">查看更多</div>
    </footer>
    <footer v-if="!auto" @click="trans()">
      <div style="border: 0;">没有更多了</div>
    </footer>
    <webpage-footer></webpage-footer>
  </div>
</template>
<script>
import Axios from 'axios'
import webpageFooter from './webpage/webpage-footer'
export default {
  data () {
    return {
      activitylist: [],
      nowP: 1,
      total: 0,
      once: 10,
      auto: true
    }
  },
  components: {
    webpageFooter
  },
  mounted () {
    Axios.get('/activity').then(res => {
      console.log(res.data)
      this.activitylist = res.data.mydata.list
      this.total = res.data.mydata.zcoolCommPage.total
    })
  },
  methods: {
    handleClick (activityId) {
      Axios.post('/activity', {
        activityId: activityId
      }).then(res => {
        // console.log(res.data)
        activityId = res.data.myId.data.id
        // console.log(activityId)
      })
      sessionStorage.setItem('olineId', activityId)
      this.$router.push(`/activitydetails/${activityId}`)
    },
    getData () {
      this.nowP++
      Axios.post('/activity', {
        nowP: this.nowP
      }).then(res => {
        this.activitylist = [...this.activitylist, ...res.data.newdata.list]
        this.nowP = this.nowP
      })
    },
    trans () {
      if (this.total <= this.nowP * this.once || this.total < this.once) {
        this.auto = !this.auto
      }
    }
  }
}
</script>
<style lang="scss" scoped>
div {
  h2 {
    font-size: 24px;
    padding:.2rem .15rem .1rem;
    line-height: 0.28rem;
    background: #fff;
    color: #333;
  }
  ul {
    margin-top: 0.1rem;
    li {
      list-style: none;
      // display: flex;
      section{
        padding: 0.1rem 0.15rem;
        &::after {
        content: "";
        display: block;
        height: 0;
        clear: both;
      }
      .imgbox {
        float: left;
        border-radius: 0.05rem;
        margin-top: 0;
        overflow: hidden;
        height: 1.2rem;
        width: 1.59rem;
        img {
          width: 1.59rem;
        }
      }
      .titlebox {
        position: relative;
        margin-left: 0.1rem;
        margin-top: 0;
        padding: 0.1rem 0;
        float: left;
        width: 1.59rem;
        height: 1.2rem;
        h3 {
          font-size: 16px;
          font-weight: 600;
          color: #333;
          width: 100%;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2; // 多行文本溢出隐藏
        }
        span {
          font-size: 12px;
          display: inline-block;
          position: absolute;
          left: 0;
          color: #999;
          &.username {
            bottom: 0.5rem;
            color: #333;
          }
          &:nth-of-type(2){
            bottom: 0.3rem;
          }
          &:nth-of-type(3) {
            bottom: 0.3rem;
            left: 0.65rem;
          }
          svg {
            margin-right: 0.04rem;
          }
        }
      }

    }
    .label{
      font-size: 13px;
      padding: 0 .15rem;
      color: #333;
      span:nth-of-type(2){
        color: #999;
        float: right;
      }
    }
      }
  }
  footer{
    padding: .2rem .12rem .36rem;
    display: flex;
    align-items: center;
    justify-content: center;
    div{
      border: 1px solid #666666;
      color:#666;
      border-radius: .05rem;
      text-align: center;
      font-size: 14px;
      width: 1.75rem;
      height: .4rem;
      line-height: .4rem;
    }
  }
}
</style>
