<template>
  <div>
    <div class="header">
      <h2>职位详情</h2>
    </div>
    <div class="total" v-if="jobdatalist.post">
      <div class="mi">
        <div class="job1">
          <p class="one">{{jobdatalist.post_name}}</p>
          <p class="two">{{jobdatalist.post_salary}}</p>
        </div>
        <div class="job2">
          <p>{{jobdatalist.city}}</p>
          <p>{{jobdatalist.experience_name}}</p>
          <p>{{jobdatalist.diploma_name}}</p>
          <p>{{jobdatalist.publishTime}}</p>
          <br />
        </div>
        <div class="job3">
          职位诱惑:
          <p>{{jobdatalist.postTag}}</p>
        </div>
        <div class="job4" @click="handleJob4(jobdatalist.corp_uid)">
          <img :src="jobdatalist.logo" class="three" />
          <div class="job7">
            <p class="four">{{jobdatalist.name_ltd}}</p>
            <p class="five">{{jobdatalist.industry_name}}</p>
            <p class="five">{{jobdatalist.stage_name}}</p>
            <p class="five">{{jobdatalist.size_name}}</p>
          </div>
        </div>
      </div>
      <div class="job5">
        <div class="de">
          <h3>职位描述</h3>
        </div>
        <div class="ms" v-html="jobdatalist.post.requirement">{{jobdatalist.post.requirement}}</div>
      </div>
      <div class="job6">
        <div class="jz">
          <h3>工作地点</h3>
        </div>
        <div class="dd">{{jobdatalist.post.area}}</div>
      </div>
      <div class="footer">
        <div class="button" @click="handleClick()">发送简历</div>
      </div>
    </div>
  </div>
</template>
<script>
// import Vue from 'vue'
import { Indicator } from 'mint-ui'
import Vue from 'vue'
import axios from 'axios'
Vue.use('Indicator')
export default {
  data () {
    return {
      jobdatalist: {}
    }
  },
  mounted () {
    Indicator.open({
      text: '正在加载中...',
      spinnerType: 'fading-circle'
    })
    axios({
      url: `job/api/postwap/postdetail.do?post_id=${this.$route.params.myid}`
      // https://m.zcool.com.cn/job/api/postwap/postdetail.do?post_id=40990
    }).then(res => {
      // console.log(res.data.data.post.requirement)
      // console.log(res.data.data)
      this.jobdatalist = res.data.data
      Indicator.close()
    })
  },
  methods: {
    handleClick () {
      this.$router.push('/login')
    },
    handleJob4 (id) {
      // console.log(id)
      this.$store.state.jobId = id
      this.$router.push({
        path: '/company',
        query: {
          id: id
        }
      })
    }
  }
}
</script>
