<template>
  <div>
    <div>
        <div class="box1" @touchmove.prevent>
          <svg class="icon thh" aria-hidden="true">
  <use xlink:href="#icon-category"></use>
</svg>
         <div class="hot">站酷招聘</div>
   <div class="global" @click="handleDown()">{{city}}
     <i class="tubiao1">
       <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-moreunfold"></use>
      </svg>
      </i>
    </div>
         <Down @myevent="handleEvent" v-if="showDown"></Down>
        <router-link to='/search'><input type="text" class="search" placeholder="搜索职位或公司" /></router-link>
         </div>
         <div class="middle">- 热门职位 -</div>
    </div>
    <ul>
        <li v-for="data in datalist" :key="data.post_id" @click="handleClick(data.post_id)">
          <div class="box6">
          <div class="box4">
            <img :src="data.logo"/>
          </div>
          <div class="box5">
            <div class="box2">
            <p class="head">{{data.post_name}}</p><br>
            <p class="mz">{{data.name_ltd}}</p>
            <p class="ys">{{data.post_salary}}</p>
            </div>
            <div class="box3">
            <p class="p1">{{data.city}}</p>
            <p class="p2">{{data.experience_name}}</p>
            <p>{{data.diploma_name}}</p>
            <p class="time">{{data.publishTime}}</p>
            </div>
          </div>
          </div>
        </li>
    </ul>
    <div class="last">
      <div v-if="isShow">
        <div class="left" >登录查看专属职位</div>
     <router-link to='login'> <div class="right" >登录</div></router-link>
      </div>

      <div class="up">
        <div class="one_left">职位</div>
       <div class="one_right" @click="handleMine()">我的</div>
      </div>
    </div>
  </div>
</template>
<script>
import Down from '@/components/Down'
import { Indicator } from 'mint-ui'
import Vue from 'vue'
import axios from 'axios'
Vue.use('Indicator')
export default {
  data () {
    return {
      datalist: [],
      // post_salary:"",
      showDown: false,
      city: '全国',
      isShow: true
    }
  },
  components: {
    Down
  },
  mounted () {
    if (!localStorage.getItem('token')) {
      this.isShow = true
    } else {
      this.isShow = false
    }
    axios({
      url: `job/api/hotPosition.do?cityId=${localStorage.getItem('post_id')}&pageNo=1&pageSize=10`
    }).then(res => {
      // console.log(res.data)
      this.datalist = res.data.data.data
      // console.log(res.data.data.name_ltd)
      Indicator.close()
    })
    Indicator.open({
      text: '正在加载中...',
      spinnerType: 'fading-circle'
    })
  },
  methods: {
    handleClick (jobid) {
      this.$router.push(`/JobDetail/${jobid}`)
    },
    handleEvent (data, name) {
      this.datalist = data
      this.showDown = false
      this.city = name
    },
    handleDown () {
      this.showDown = !this.showDown
    },
    handleMine () {
      if (!localStorage.getItem('token')) {
        this.$router.push('/login')
      } else {
        this.$router.push('/center')
      }
    }
  }

}

</script>
<style lang="scss" scoped>
.box1{
  // padding:.144rem .192rem;
  // overflow: hidden;
  position:relative;
  font-size:20px;
  background:#ffd101;
  height:1rem;
  .thh{font-size:0rem;
  padding-bottom:.05rem;
  padding-left:.192rem;
  }
  .hot{
    display: inline;
    font-size:24px;
    text-align: center;
    // line-height:.8rem;
    font-weight: normal;
    padding-left:1.3rem;
  }
  p{margin:0px}
  .global{
    font-size:16px;
    position: relative;
    padding-left:.192rem;
    top:.2rem;
    // z-index:-9999;
  }
  .tubiao1{font-size:.2rem;
  top:36%;left:15%;height:.4rem}
 .search{
      display:inline-block;
      width:1.8rem;
      line-height:.26rem;
      border-radius:.1rem;
      border:1px solid #ccc;
      text-align: center;
      color:#999;
      margin-left:1rem;
      position: absolute;
      bottom: .2rem;
  }
}
 .middle{
   height:.6rem;
   line-height: .6rem;
   text-align: center;
   color:#ccc;
   background:#eee;
   font-size:20px;
 }
ul{
    margin-bottom: .8rem;
    // font-size:20px;
    background:#eee;
    list-style:none;
    li{
      font-size:16px;
      // padding:.144rem .192rem;
      height:1.2rem;
      background:#fff;
      margin-top:.1rem;
      box-sizing: border-box;
      .box6{
        // overflow: hidden;
        padding:.144rem .192rem;
      }
      img{
      width:.788rem;
      height:.768rem;
      border:1px solid #ededed;
      background-position: 50%;
      background-size:100% auto;
      float:left;
    }
    .box5{
      padding-left:.1rem;
      overflow: hidden;
    .box2{
      border-bottom: 1px solid #ccc;
      overflow: hidden;
      position:relative;
      // padding-top:.1rem;
      .head{font-weight:bold;}
      .mz{padding:.08rem 0;}
      .head,.mz{
        display:inline-block;
      }
      .ys{
        color:#ff8122;
        float: right;
        position: absolute;
        left:60%;
        top:50%;
        padding-left:.3rem;
      }

    }
    .box3{
      padding-top:.1rem;
      overflow: hidden;
      font-size:12px;
     p{
       float:left;
       color:#ccc;
       padding:0 .04rem;
     }
     .p1,.p2{
         border-right:2px solid #ccc;
         padding:0 .02rem;

       }
       .time{
         float:right;
       }
    }
    }
    }
}
.last{
  width:100%;
  // height:1rem;
  background:yellow;
  font-size:16px;
  position:fixed;
  bottom:0;
  .left{
    background:rgba(0,0,0,.8);
    color:#ffd100;
    text-align: center;
    width:70%;
    height:.4rem;
    line-height:.4rem;
    float:left;
  }
  .right{
      height:.4rem;
      line-height:.4rem;
      background:rgba(255,209,0,.8);
      text-align: center;
      width:30%;
      float:left;
    }
    .up{
      background: black;
      height:.6rem;
      width:100%;
      line-height: .6rem;
      float:left;
      .one_left{
        color: #ffd101;
        float:left;
        width:50%;
        text-align: center;
      }
      .one_right{
        float:left;
        color: #fff;
        width:50%;
        text-align: center;
      }
    }
}
</style>
